import React from 'react'
import { AppImages } from '../Asset/images/image';

const ComingSoon = () => {
  const backgroundImageStyle = {
    backgroundImage: `url(${AppImages.bg})`,

  };

  return (
    <div className="bg-cover bg-center h-screen" style={backgroundImageStyle}>
      <div className="flex justify-center">
        <img
          src={AppImages.gallerialogo}
          className="w-[40%] sm:w-[20%]"
          alt=""
          srcset=""
        />
      </div>
      <div className="text-center text-[#f1b44b] text-[39px] sm:text-[60px] sm:pt-[17%]  pt-[38%]  font-semibold font-serif">
        Launching Soon . .
        {/* <div className="absolute top-0 left-0 w-full h-full bg-white opacity-10 filter blur-md rounded-lg"></div> */}
      </div>
    </div>
  );
}

export default ComingSoon